import React, { useState, useEffect, useRef } from "react";
import Country from "./components/Country";
import NewCountry from "./components/NewCountry";
import { AppBar, Toolbar, Typography, Badge, Grid } from "@mui/material";
import axios from "axios";
import "./App.css";

const App = () => {
  const [countries, setCountries] = useState([]);
  const apiEndpoint = "https://medals-api-tmb.azurewebsites.net/api/country";

  const medals = useRef([
    { id: 1, name: "gold", color: "gold" },
    { id: 2, name: "silver", color: "silver" },
    { id: 3, name: "bronze", color: "#b08d57" },
  ]);

  // this is the functional equivalent to componentDidMount
  useEffect(() => {
    // initial data loaded here
    async function fetchCountries() {
      const { data: fetchedCountries } = await axios.get(apiEndpoint);
      setCountries(fetchedCountries);
    }
    fetchCountries();
  }, []);

  const handleAdd = async (name) => {
    // const id = countries.length === 0 ? 1 : Math.max(...countries.map(country => country.id)) + 1;
    // setCountries([...countries].concat({ id: id, name: name, gold: 0, silver: 0, bronze: 0 }));
    const { data: post } = await axios.post(apiEndpoint, { name: name });
    setCountries(countries.concat(post));
  };
  const handleDelete = async (countryId) => {
    // setCountries([...countries].filter(c => c.id !== countryId));
    const originalCountries = countries;
    setCountries(countries.filter((c) => c.id !== countryId));
    try {
      await axios.delete(`${apiEndpoint}/${countryId}`);
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        console.log("Record not found");
      } else {
        alert("An error occurred");
        setCountries(originalCountries);
      }
    }
  };
  const handleIncrement = (countryId, medalName) => {
    const idx = countries.findIndex((c) => c.id === countryId);
    const mutableCountries = [...countries];
    mutableCountries[idx][medalName] += 1;
    setCountries(mutableCountries);
  };
  const handleDecrement = (countryId, medalName) => {
    const idx = countries.findIndex((c) => c.id === countryId);
    const mutableCountries = [...countries];
    mutableCountries[idx][medalName] -= 1;
    setCountries(mutableCountries);
  };
  const getAllMedalsTotal = () => {
    let sum = 0;
    medals.current.forEach((medal) => {
      sum += countries.reduce((a, b) => a + b[medal.name], 0);
    });
    return sum;
  };
  return (
    <React.Fragment>
      <AppBar position="static" sx={{ bgcolor: "#404040" }}>
        <Toolbar variant="dense">
          <Typography
            variant="h6"
            color="inherit"
            component="div"
            fontFamily="Serif"
            fontWeight="Bold"
            fontSize="30px"
            sx={{ mt: 1 }}
          >
            <Badge color="secondary" badgeContent={getAllMedalsTotal()}>
              Olympic Medals
            </Badge>
          </Typography>
        </Toolbar>
      </AppBar>
      <Grid
        container
        className="countries"
        justifyContent="left"
        px={8}
        spacing={{ xs: 2 }}
        columns={{ xs: 4, sm: 8, md: 12 }}
      >
        {countries.map((country) => (
          <Grid item key={country.id}>
            <Country
              country={country}
              medals={medals.current}
              onDelete={handleDelete}
              onIncrement={handleIncrement}
              onDecrement={handleDecrement}
            />
          </Grid>
        ))}
      </Grid>
      <NewCountry onAdd={handleAdd} />
    </React.Fragment>
  );
};

export default App;
